.container {
  border-radius: 10px;
  background-color: #fff;
  transition: all 0.3s linear;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1);
}
.container:hover {
  transform: scale(1.02);
}
.image {
  width: 100%;
  border-radius: 10px 10px 0 0;
}
.description {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.price {
  color: #fff;
  margin-top: 10px;
  padding: 5px 10px;
  width: max-content;
  background: green;
  border-radius: 50px;
}
.iconBox {
  display: flex;
  flex-direction: column;
}
.iconBoxHeart {
  max-width: 25px;
  padding: 5px;
  cursor: pointer;
  margin-bottom: 10px;
  border-radius: 50px;
  transition: all 0.3s linear;
  background-color: #ccc;
}
.iconBoxHeart:hover {
  background-color: #ff0000;
}
.iconBoxPlus {
  max-width: 25px;
  padding: 5px;
  cursor: pointer;
  margin-bottom: 10px;
  border-radius: 50px;
  transition: all 0.3s linear;
  background-color: #ccc;
}
.iconBoxPlus:hover {
  background-color: #00ff44;
}
@media screen and (max-width: 768px) {
  .container {
    margin-bottom: 20px;
  }
}
