* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: #e7e7e7;
  margin: 0 auto;
  max-width: 1024px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
@media screen and (max-width: 1024px) {
  body {
    padding: 10px;
  }
}

h2 {
  font-size: 1.7rem;
}
@media screen and (max-width: 1024px) {
  h2 {
    font-size: 1.5rem;
  }
}

h3 {
  font-size: 1.5rem;
}
@media screen and (max-width: 1024px) {
  h3 {
    font-size: 1.25rem;
  }
}

h4 {
  font-size: 1.25rem;
}
@media screen and (max-width: 1024px) {
  h4 {
    font-size: 1rem;
  }
}